import React, { Component } from 'react';
import PropTypes from 'prop-types';
import ChatBot from 'react-simple-chatbot';
import axios from 'axios';
import { ThemeProvider } from 'styled-components';

const theme = {
  background: '#f5f8fb',
  fontFamily: 'Helvetica Neue',
  headerBgColor: '#5280F7',
  headerFontColor: '#fff',
  headerFontSize: '15px',
  botBubbleColor: '#D9D9D9',
  botFontColor: '#4a4a4a',
  userBubbleColor: '#CED5E8',
  userFontColor: '#4a4a4a',
};

// 
class Review extends Component {
  constructor(props) {
    super(props);

    this.state = {
      building: '',
      residentail: '',
      location: '',
      price: '',
      scope: '',
    };
  }

  componentWillMount() {
    const { steps } = this.props;
    const { building, residentail, location, price, scope } = steps;

    this.setState({ building, residentail, location, price, scope });
  }

  render() {
    const { building, residentail, location, price, scope } = this.state;
    return (
      <div style={{ width: '100%' }}>
        <h4>***  ***</h4>
        <table>
          <tbody>
            <tr>
              <td> :   </td>
              <td>{building.value}</td>
            </tr>
            <tr>
              <td> :   </td>
              <td>{residentail.value}</td>
            </tr>
            <tr>
              <td>:   </td>
              <td>{location.value}</td>
            </tr>
            <tr>
              <td>:   </td>
              <td>{price.value}</td>
            </tr>
            <tr>
              <td> :   </td>
              <td>{scope.value}</td>
            </tr>
          </tbody>
        </table>
      </div>
    );
  }
}

Review.propTypes = {
  steps: PropTypes.object,
};

Review.defaultProps = {
  steps: undefined,
};

// POST
class Submit extends Component {
  handleSubmit = async (event) => {
    console.log('Button clicked');
    event.preventDefault(); //   

    const { steps, triggerNextStep } = this.props;
    if (!steps) return; // steps    

    const { value: building } = steps.building || {};
    const { value: residentail } = steps.residentail || {};
    const { value: location } = steps.location || {};
    const { value: price } = steps.price || {};
    const { value: scope } = steps.scope || {};
    const { value: additionalConditions } = steps.additionalConditions || {};

    //   
    const data = {
      building: building || '',
      residentail: residentail || '',
      location: location || '',
      price: parseInt(price) || 0,
      scope: parseInt(scope) || 0,
      additionalConditions: additionalConditions || '',
    };
    console.log(data); //  
    try {
      const response = await axios.post('http://ceprj.gachon.ac.kr:60015/model', data);
      // POST      
      console.log(response.data);
      //     
      triggerNextStep({ value: response.data });
    } catch (error) {
      console.error(error);
      triggerNextStep(); //       
    }
  };

  render() {
    return (
      <div>
        <button onClick={this.handleSubmit}>  </button>
      </div>
    );
  }
}

class Response extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    const { previousStep } = this.props;
    const { value } = previousStep;
    const linkRegex = /(http:\/\/[^\s]+)/gi; //    
    return (
      <div>
        {value &&
          value.split('\n').map((line, index) => {
            const linkMatch = line.match(linkRegex); //  
            if (linkMatch) {
              //   
              const link = linkMatch[0];
              const text = line.replace(linkRegex, '').trim(); //   
              return (
                <p key={index}>
                  {text}
                  <a href={link} target="_blank" rel="noopener noreferrer">
                    Link
                  </a>
                </p>
              );
            } else {
              //   
              return <p key={index}>{line}</p>;
            }
          })}
      </div>
    );
  }
}
const CustomIcon = () => (
  <img src="/images/logo.png" style={{ width: '80px', height: '80px' }} alt="Chatbot Icon" />
);

class MyChatbot extends Component {
  render() {
    return (
      <ThemeProvider theme={theme}>
        <ChatBot
          floating
          botAvatar="/images/bot-avatar.png"
          userAvatar="/images/user-avatar.png"
          floatingIcon={<CustomIcon />}
          floatingStyle={{width:"100px", height:"100px"}}
          headerTitle="HOMEMATE CHATBOT"
          steps={[
            {
              id: '1',
              message: '///     ?',
              trigger: 'building',
            },
            {
              id: 'building',
              user: true,
              trigger: '3',
            },
            {
              id: '3',
              message: '//     ?   !',
              trigger: 'residentail',
            },
            {
              id: 'residentail',
              user: true,
              trigger: '5',
            },
            {
              id: '5',
              message: '/     . (:   )       .',
              trigger: 'location',
            },
            {
              id: 'location',
              user: true,
              trigger: '7',
            },
            {
              id: '7',
              message: '  . (   )   ,(: 3000,50)  !',
              trigger: 'price',
            },
            {
              id: 'price',
              user: true,
              trigger: '9',
            },
            {
              id: '9',
              message: '  . ,  ',
              trigger: 'scope',
            },
            {
              id: 'scope',
              user: true,
              trigger: 'review',
            },
            {
              id: 'review',
              component: <Review />,
              trigger: '12',
            },
            {
              id: '12',
              message: '     ! ,     :) (:  ,    )',
              trigger: 'additionalConditions',
            },
            {
              id: 'additionalConditions',
              user: true,
              trigger: 'wait-message',
            },
           
            {
              id: 'wait-message',
              message: '   !  ...',
              trigger: 'submit-button',
            },
            {
              id: 'submit-button',
              component: <Submit />,
              waitAction: true,
              trigger: '16',
            },
            {
              id: '16',
              component: <Response />,
             
            },
          ]}
        />
      </ThemeProvider>
    );
  }
}

export default MyChatbot;
